
.project {
	padding: 0 0 70px;

	@include maxw(576px) {
		padding: 0 0 40px;
	}

	&__video {
		height: 53.13vw;

		@include minw(1921px) {
			height: 1000px;
		}

		@include maxw(576px) {
			height: 285px;
			margin: 0 -20px;
		}

		iframe {
			display: block;
			width: 100%;
			height: 100%;
		}
	}

	&__main {
		padding: 37px 0 0;

		@include maxw(640px) {
			padding: 20px 0 0;
		}
	}

	&__title {
		margin-bottom: 30px;
		font-size: 45px;
		line-height: 1.31;
		font-weight: 400;
		text-transform: uppercase;

		@include maxw(640px) {
			font-size: 30px;
			margin-bottom: 20px;
		}
	}

	&__desc {
		margin-bottom: 50px;

		@include maxw(1000px) {
			margin-bottom: 40px;
		}

		@include maxw(640px) {
			margin-bottom: 20px;
		}

		p {
			margin-bottom: 28px;

			@include maxw(640px) {
				font-size: 15px;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.gallery {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		&__item {
			width: 49.3%;
			height: 36.11vw;
			margin-bottom: 22px;

			@include maxw(850px) {
				width: 100%;
				height: 58.19vw;
			}

			@include maxw(360px) {
				height: 202px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		&__big {
			width: 100%;
			height: 58.19vw;

			@include minw(1921px) {
				height: 1000px;
			}

			@include maxw(360px) {
				height: 202px;
			}
		}
	}
}